* {
  box-sizing: border-box;
	padding:0;
}

#container{
  display: block;
margin: 50px;
border: 1px;
color: #434747;

font-family: 'Times New Roman', serif;
}

.smallBlock{
  border: 5px solid #C0C0C0;

  }

/* Style the header */
header {
  background: linear-gradient(-90deg, #9dcccf, #c0cec4);
  padding: 30px 30px 30px;
  border-radius: 10px;
  
}

/* Style the header text */
h1 {
  color: #9e5a5a;
  border-bottom: 1px rgb(104, 87, 121) solid;
}

/* Style the button checkout */
#finish {
  float: right;
  width: 150px;
  height: 40px;
  color: white;
  background-color: #812929;
}

#admin {
  float: right;
  background-color: rgb(115, 155, 138);
  padding: 12px 16px; /* Some padding */
  cursor: pointer; /* Mouse pointer on hover */
  border-radius: 5px;
  border: 2px solid rgb(122, 85, 116);
}
/* Style names of grocery categories*/
h2 {
  border: 3px solid rgb(117, 111, 98);
  border-radius: 10px;
}

.images img{

  width: 42%;
	height: 50%;
  padding: 5px 10px;
  border-right: none;
	border-radius: 20px 0px 0 20px;
	padding: 5px 10px;
	float: right;
}

/* The sidebar menu */
#groceryList {
  height: 100%; /* Full-height: remove this if you want "auto" height */
  width: 160px; /* Set the width of the sidebar */
  top: 45%; /* Stay at the top */
  background-color:  rgb(115, 155, 138);
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 20px;
}


.main {
  margin-left: 160px; /* Same as the width of the sidenav */
  font-size: 28px; /* Increased text to enable scrolling */
  padding: 0px 10px;
}

.topmenu{
  overflow: hidden;
  background-color: rgb(115, 155, 138);
  top: auto;
  width: auto;
  border-radius: 5px;
}
.topmenu a, #groceryList a{
  float: left;
  display: block;
  color: #3f3a3a;
  text-align: center;
  padding: 17px 16px;
  text-decoration: none;
}
#groceryList a{
  width: 90px; /* Set the width of the sidebar */
}

.topmenu a:hover, #groceryList a:hover {
  background: #ddd;
}

.topmenu input[type=text] {
  border: 1px solid #ccc;  
  margin-top: 18px;
}


li {
  display: inline-block;
  padding: 10px;
}

/* Darker background on mouse-over */
.btn:hover {
  background-color: rgb(121, 34, 7);
  border-radius: 10px;
}

/* Style buttons (name of items) in the ol */
.itemNames {
  background-color: rgb(199, 214, 204); /* Blue background */
  font-size: 16px; /* Set a font size */
  cursor: pointer; /* Mouse pointer on hover */
  border-radius: 5px;
  border: 3px solid rgb(107, 87, 44);
  width: 150px;
  height: 150px;
}

/* Style result items table */
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 70%;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  height: 20px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

/* Style the footer */
footer {
  background-color: rgb(115, 155, 138);
  padding: 10px;
  text-align: center;
  color: white;
}

#login-user,
#new-user,
#admin-Login,
#form-fruit,
#adminOperations,
#form-Add,
#form-Update,
#form-Delete {
  max-width: 500px;
  margin: auto;
  background-color: rgb(115, 155, 138);
  padding: 12px 16px; /* Some padding */
  font-size: 16px; /* Set a font size */
  cursor: pointer; /* Mouse pointer on hover */
  border-radius: 5px;
  border: 2px solid rgb(134, 87, 101);
}



#register-new-user {
  position: absolute;
  top: 55%;
  left: 40%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}


